@import url('https://fonts.googleapis.com/css2?family=Lato:wght@700&family=Lobster&family=Oxanium:wght@500&family=Quicksand&family=Roboto&display=swap');

.mainMintdiv{
    width: 100%;
    height: auto !important;
    background-color: #210234;
}
.model_bg{
    background-color: #210234;

}
.Firstdivheading{
    color: rgb(192,54,113);
    font-family: sans-serif;
    font-size: 23px;
    text-align: center;

}
.ConnectDiv{
    color: rgb(192,54,113);
    font-family: sans-serif;
    font-size: 50px;
    text-align: center;
    font-weight: 600;

}
.ConnectdivPP{

    color: white;
    font-family: quicksand, sans-serif;
    font-size: 16px;
    text-align: center;
    margin-top: 2rem;

}
.Connectbtn{
    border-radius: 25px;
    background-color: #ffa05f;
    color: #ffffff;
    font-family: quicksand, sans-serif;
    font-size: 16px;
    font-weight: bold;
    padding: 10px 35px 10px 35px;
   

}
.btnconnecthere{
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.afterconnecttext{

    color: white;
    font-family: quicksand, sans-serif;
    font-size: 15px;
    text-align: center;
    margin-top: 1rem;

}
.Salepricetext{
    color: white;
    font-family: quicksand, sans-serif;
    font-size: 25px;
    text-align: center;
    margin-top: 1rem;
    font-weight: 600;
}

.smbtnplus{
    border-radius: 500px;
    background-color: #ffffff;
    margin-left: 1rem;
    margin-right: 1rem;

}

@media (min-width:481px) and (max-width:1024px) {
    .mainMintdiv{
        width: 100%;
        height: auto;
        background-color: #000;
    }

}

@media (min-width:280px) and (max-width:480px) {
.innerMintdiv{
    padding: 0 20px 0 20px;
}

.ConnectDiv{
    color: #ffffff;
    font-family: quicksand, sans-serif;
    font-size: 25px;
    text-align: center;
    font-weight: 600;

}
.mainMintdiv{
    width: 100%;
    height: auto;
    background-color: #000;
}

    
}


.modelbtnhere{
    background-color: #ffa05f;
color: #ffffff;
}